import { Route, Routes, useLocation, useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import Navbar from "./Component/Components/Navbar/Navbar";
import Home from "./Component/Pages/Home/Home";
import Footer from "./Component/Components/Footer/Footer";
import Services from "./Component/Pages/services/Services";
import About from "./Component/Pages/About/About";
import Contact from "./Component/Pages/About/Contact/Contact";
import SendData from "./Component/Pages/FormSendData/SendData";
import Admin from "./Component/Pages/AdminDashbord/Admin";
import Login from "./Component/Pages/AdminDashbord/Login";
import Dashboard from "./Component/Pages/AdminDashbord/Admin"; //   

function App() {
  const location = useLocation();
  const navigate = useNavigate();
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  const hideNavbarFooter = location.pathname === "/Admin" || location.pathname === "/Dashboard";

  useEffect(() => {
    if (location.pathname === "/Admin" && !isLoggedIn) {
      navigate("/Login");
    }
  }, [location, isLoggedIn, navigate]);

  const handleLogin = (email, password) => {
    //       
    if (email === "rabea" && password === "rabea") {
      setIsLoggedIn(true);
      navigate("/Admin");
    } else {
      alert("      ");
    }
  };

  return (
    <div dir="rtl" className="App">
      <div>
        {!hideNavbarFooter && <Navbar />}
        <Routes>
          <Route index element={<Home />} />
          <Route path="/services" element={<Services />} />
          <Route path="/about" element={<About />} />
          <Route path="/Contact" element={<Contact />} />
          <Route path="/SendData" element={<SendData />} />
          <Route path="/Admin" element={<Admin />} />
          <Route path="/Login" element={<Login onLogin={handleLogin} />} />
          <Route path="/Dashboard" element={<Dashboard />} /> {/*     */}
        </Routes>
        {!hideNavbarFooter && <Footer />}
      </div>
    </div>
  );
}

export default App;
